@import 'assets/scss/variables.scss';

.container {
    height: 100%;
    padding: 0 17.2rem;
    width: calc(100% - 17.2rem * 2);
    font-family: $tertiaryFont;
    & :global(.tab-container) {
        & :global(.tab-buttons) {
            margin: 0 -17.2rem;
            padding: 0 17.2rem 1rem;
            border-bottom: 1px solid rgba($dark, 15%);
            display: flex;
            gap: 1rem;
            & :global(.tab-button) {
                padding: 0.65rem 1rem;
                border-radius: 2rem;
                border: 1px solid rgba($dark, 25%);
                background-color: transparent;
                font-size: $textSmall;
                cursor: pointer;
                font-family: $tertiaryFont;
                &:hover, &:global(.active) {
                    background-color: rgba($googleBlue, 0.15);
                    border-color: $googleBlue;
                    color: $googleBlue;
                }
            }
        }
        & :global(.tab-content) {
            max-height: calc(100vh - 11rem);
            overflow: auto;
            margin: 0 -17.2rem;
            padding: 0 17.2rem;
        }
    }
}