@import 'assets/scss/variables.scss';

.wrapper {
    color: $dark;
    padding: 2.5rem 0 1.5rem;
    & .main-title {
        font-size: calc($textExtraLarge - 0.5rem);
        font-weight: 400;
    }
    & .designation {
        color: rgba($dark, 0.85);
    }
    & .secondary-info {
        margin-top: 2rem;
        gap: 1rem;
        & .images {
            border-radius: 1rem;
            overflow: hidden;
            width: fit-content;
            & > img {
                height: 30rem;
                border-right: 0.35rem solid $light;
            }
            & .secondary-images {
                & img {
                    height: calc(15rem - 0.175rem);
                    &:first-child {
                        border-bottom: 0.35rem solid $light;
                    }
                }
            }
        }
        & .linkedin-description {
            width: 20rem;
            background-color: rgba($dark, 0.045);
            border-radius: 1rem;
            padding: 1.5rem;
            & .icon-wrapper {
                margin-bottom: 0.5rem;
                & svg {
                    height: 1rem;
                    width: 1rem;
                    margin-right: 0.5rem;
                }
            }
            & > p {
                margin-top: 0.25rem;
                opacity: 0.75;
            }
        }
        & .other-social-links {
            max-width: 20%;
            gap: 1rem;
            & .github, & .behance {
                border-radius: 1rem;
                background-color: rgba($dark, 0.125);
                height: 50%;
                padding: 1rem;
                background-size: cover;
                position: relative;
                & .icon-wrapper {
                    height: 1.5rem;
                    color: $light;
                    z-index: 1;
                    & p {
                        font-size: calc($textSmall - 0.25rem);
                    }
                }
                & svg {
                    height: 1rem;
                    width: 1rem;
                    margin-right: 0.5rem;
                    fill: $light;
                }
                &:after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: rgba($dark, 0.45);
                    left: 0;
                    top: 0;
                    border-radius: 1rem;
                }
            }
        }
    }
    & .detailed-description {
        margin-top: 3.5rem;
        padding-top: 2rem;
        border-top: 1px solid rgba($dark, 15%);
        display: flex;
        & .search-links {
            width: 70%;
            & .search-links-item {
                margin-bottom: 3.5rem;
                padding-right: 1rem;
                & svg {
                    height: 1.75rem;
                    width: 1.75rem;
                    margin-right: 1.25rem;
                }
                & h3 {
                    font-size: $textSmall;
                }
                & .link {
                    color: $darkShadeOne;
                }
                & h2 {
                    color: $linkTwo;
                    margin: 1.5rem 0 0.5rem;
                    font-weight: 400;
                }
                & .section-description {
                    font-size: $textSmall;
                    font-weight: 300;
                    color: rgba($dark, 0.85);
                }
            }
        }
        & .about {
            width: 30%;
            border-left: 1px solid rgba($dark, 15%);
            padding-left: 2rem;
            margin-bottom: 2.5rem;
        }
        & .section-title {
            margin-bottom: 1rem;
            font-size: $textMedium;
            font-weight: 400;
        }
        & .description {
            font-size: $textSmall;
            font-weight: 300;
            color: rgba($dark, 0.85);
        }
    }
}