@import 'assets/scss/variables.scss';

.wrapper {
    color: $dark;
    padding: 0.75rem 0;
    & .container {
        position: relative;
        max-width: 1128px;
        margin: auto;
        & .logo {
            margin-right: 1rem;
            & svg {
                height: 3.25rem;
                width: 3.25rem;
            }
        }
        & :global(.floating-button-general) {
            & svg {
                fill: $dark;
            }
        }
        & .column {
            width: 50%;
            &:last-child {
                margin: -0.75rem 5rem -0.75rem 0;
                border-right: 1px solid rgba($dark, 0.125);
                padding-right: 2.5rem;
            }
        }
    }
   
    & .page-actions {
        gap: 0.25rem;
        & .icon {
            width: 6.5rem;
            color: rgba($dark, 0.6);
            cursor: pointer;
            transition: ease-in-out all 0.05s;
            & svg, & svg * {
                height: 2.25rem;
                width: 2.25rem;
                margin-bottom: 0.25rem;
                fill: rgba($dark, 0.6);
                transition: ease-in-out all 0.05s;
            }
            &:hover {
                color: rgba($dark, 0.9);
                & svg, & svg * {
                    fill: rgba($dark, 0.9);
                }
            }
        }
    }
    :global(.linkedin-header) {
        & :global(.side-panel-global) {
            background-color: $light !important;
            & header {
                padding: 1.855rem 2rem !important;
                & svg {
                    fill: $danger !important;
                }
            }
            & ul {
                & li {
                    color: $dark !important;
                    &:hover {
                        color: $dark !important;
                    }
                }
            }
        }
    }
}
