@import 'assets/scss/variables.scss';

.wrapper {
    position: relative;
    min-width: 15rem;
    & input, & textarea {
        padding: 0.625rem 1rem;
        border-radius: 5px;
        border: 2px solid transparent;
        background-color: rgba($gray , 0.15);
        line-height: 1.5;
        width: 100%;
        resize: none;
        &:focus {
            border-color: $secondaryMain;
        }
        &:focus-visible {
            border-color: $secondaryMain;
            outline: $secondaryMain;
        }
    }
}