@import 'assets/scss/variables.scss';

.wrapper {
    background-color: $light;
    position: relative;
    padding: 1.5rem 3rem;
    &:global(.google-header) {
        & :global(.floating-button-general) {
            & svg {
                fill: #252526;
            }
        }
        & .logo {
            height: 3rem;
            width: 9.2rem;
            margin-right: 5rem;
            & svg {
                width: 100%;
                height: 100%;
            }
        }
        & .searchbox {
            min-width: 40%;
            & input {
                font-family: $tertiaryFont;
                border-radius: 2rem;
                box-shadow: 0 2px 5px 1px rgba($dark, 0.16);
                background-color: $light;
                padding: 1rem 1.5rem;
            }
            & svg {
                fill: $googleBlue;
            }
        }
    }
}