@import 'assets/scss/variables.scss';

.wrapper {
    font-family: $primaryFont;
    @include xl {
        flex-direction: column;
        overflow-y: auto;
        margin: 0 -2rem;
        width: calc(100% + 4rem);
        justify-content: center;
    }
    & .column {
        max-height: calc(100vh - 11.5rem);
        overflow: auto;
        @include xl {
            overflow: hidden;
            max-height: 100%;
        }
        @include xl {
            padding: 5rem 0 5rem;
        }
        &:first-child {
            width: 40%;
            @include xl {
                width: calc(100% - 4rem);
                padding: 2.5rem 2rem;
            }
        }
        &:last-child {
            width: 60%;
            margin-right: -8rem;
            padding-right: 8rem;
            @include xxl {
                margin-right: -4rem;
                padding-right: 4rem;
            }
            @include xl {
                width: 100%;
                margin: 0 0 3rem;
                padding: 0;
                overflow: auto;
            }
            @include sm {
                margin: 0;
            }
        }
       
        & .contact-heading {
            font-family: $secondaryFont;
            & h2 {
                font-size: calc($textLarge + 5px);
                font-weight: 400;
                margin-bottom: 1rem;
            }
            & .commented-code {
                max-width: 40rem;
                @include xl {
                    display: none;
                }
            }
            & .email {
                margin-top: 1.5rem;
            }
            & .whatsapp {
                margin-top: 0.75rem;
            }
            & .email, .whatsapp {
                font-size: $textPrimary;
                font-family: $primaryFont;
                cursor: pointer;
                & a {
                    color: $light;
                    text-decoration: none;
                }
                & svg {
                    height: 1.5rem;
                    width: 1.5rem;
                    margin-right: 1.5rem;
                    transition: all ease-in-out 0.5s;
                    &:last-child {
                        margin-left: 1.25rem;
                        fill: $light;
                        height: 1.25rem;
                        width: 1.25rem;
                        opacity: 0;
                    }
                }
                &:hover {
                    & a {
                        color: $info;
                    }
                    & svg {
                        &:last-child {
                            opacity: 1;
                            margin-left: 1.5rem;
                        }
                    }
                    
                }
            }
        }
        & .contact-wrapper {
            min-width: 40rem;
            margin: auto;
            @include xl {
                margin: 0;
                min-width: fit-content;
                width: calc(100% - 7rem);
                padding: 2rem;
            }
           & .textfield {
                margin-bottom: 1.5rem;
                & input, & textarea {
                    font-family: $primaryFont;
                    color: $light;
                }
           } 
           & .button {
                background-color: $secondaryMain;
                color: $light;
           }
        }
    }
}