@import 'assets/scss/variables.scss';

.wrapper {
    & .cover-photo {
        height: 20rem;
        border-radius: 0.75rem 0.75rem 0 0;
        overflow: hidden;
        & img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    & .basic-details {
        background-color: $light;
        padding: 0 2.4rem 2.4rem 2.4rem;
        border-radius:  0 0 0.75rem 0.75rem;
        border: 1px solid rgba($dark, 0.15);
        border-top: 0;
        & .dp {
            width: 12.5rem;
            height: 12.5rem;
            border-radius: 50%;
            margin: -10rem 0 1.5rem;
            z-index: 1;
            position: relative;
            overflow: hidden;
            border: 0.35rem solid $light;
            & img {
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }
        & h2 {
            font-size: calc($textLarge + 0.4rem);
            font-weight: 400;
        }
        & .designation {
            font-size: $textMedium;
            font-weight: 200;
            color: rgba($dark, 0.85);
            margin-bottom: 1rem;
        }
        & .address {
            font-size: $textSmall;
            font-weight: 200;
            color: rgba($darkShadeTwo, 0.65);
        }
        & .link {
            font-weight: 500;
            font-size: $textSmall;
            color: $linkedin;
            margin-left: 1.5rem;
        }
        & .actions {
            display: flex;
            gap: 1rem;
            margin-top: 2rem;
            & .button-resume, & .button-message {
                border-radius: 2rem;
                display: flex;
                align-items: center;
                & svg {
                    height: 1rem;
                    width: 1rem;
                    margin-right: 0.5rem;
                }
            }
            & .button-resume {
                background-color: $linkedin;
                color: $light;
                & svg {
                    fill: $light;
                }
            }
        }
        & .info-wrapper {
            & .primary-details {
                width: 70%;
            }
            & .secondary-details {
                width: 30%;
                & .main-label {
                    margin-bottom: 1rem;
                    & img {
                        width: 1.75rem;
                    }
                    & h4 {
                        font-size: $textPrimary;
                        margin-left: 0.75rem;
                        font-weight: 400;
                    }
                }
            }
        }
    }
    & .section-block {
        padding: 2.4rem;
        border-radius: 0.75rem;
        background-color: $light;
        margin-top: 2.5rem;
        border: 1px solid rgba($dark, 0.15);
        & h4 {
            font-size: calc($textLarge - 0.25rem);
            margin-bottom: 1.2rem;
        }
        & .description {
            font-size: $textModerate;
            opacity: 0.75;
        }
        &.rec-block {
            margin-bottom: 2rem;
        }
    }
    & .experience, & .education {
        margin-bottom: 2.75rem;
        padding-bottom: 1.5rem;
        border-bottom: 1px solid rgba($dark, 0.05);
        & .logo {
            height: 2.5rem;
            width: auto;
            margin-right: 1rem;
            & img {
                height: 100%;
            }
        }
        & .exp-details, & .edu-details {
            & .designation {
                font-size: calc($textMedium - 0.1rem);
            }
            & .organisation {
                margin-bottom: 1rem;
            }
            & .organisation, & .degree {
                font-size: $textPrimary;
            }
            & .year, & .place {
                font-size: $textSmall;
                color: rgba($dark, 0.75);
            }
            & .place {
                margin-bottom: 1rem;
            }
            & .skills {
                list-style: none;
                font-size: calc($textSmall - 1px);
                & li {
                    margin: 0 0.5rem;
                    position: relative;
                    line-height: 1;
                    opacity: .85;
                    &:first-child {
                        font-weight: 500;
                        margin-right: 0.5rem;
                        margin-left: 0;
                        opacity: 1;
                        &:after {
                            display: none;
                        }
                    }
                    &:last-child {
                        &:after {
                            display: none;
                        }
                    }
                    &:after {
                        content: "";
                        position: absolute;
                        right: -0.65rem;
                        top: 0;
                        bottom: 0;
                        background-color: rgba($dark, 0.5);
                        border-radius: 50%;
                        width: 0.25rem;
                        height: 0.25rem;
                        margin: auto;
                    }
                }
            }
            & .university {
                font-size: calc($textPrimary + 0.1rem);
            }
        }
        &:last-child {
            margin-bottom: 0;
            border-bottom: 0;
            padding-bottom: 0;
        }
    }
    & .carousel-item {
        & .avatar-info {
            & .dp {
                width: 5rem;
                height: 5rem;
                background-color: rgba($dark, 0.05);
                border-radius: 50%;
                overflow: hidden;
                border: 1px solid rgba($dark, 0.05);
                margin-right: 1.5rem;
                & img {
                    width: 100%;
                    height: 100%;
                }
            }
            & h4 {
                font-size: $textMedium;
                margin-bottom: 0;
            }
            & h5 {
                font-size: $textSmall;
                font-weight: 400;
                color: rgba($dark, 0.75);
            }
        }
        & .msg-wrapper {
            margin-top: 2rem;
            & .message {
                font-weight: 200;
                font-size: $textPrimary;
                font-style: italic;
            }
        }
    }
    & footer {
        & p {
            font-size: $textPrimary;
            font-size: $textSmall;
            color: rgba($dark, 0.5);
            padding-bottom: 2.5rem;
            font-style: italic;
            & a {
                display: inline-block;
                color: $link;
                text-decoration: underline;
                margin-left: 0.5rem;
                cursor: pointer;
            }
        }
    }
}